body {
    font-family: 'Open Sans', sans-serif;
    margin: 0px;
    padding: px;
    background-image: url(image.png); /* Add your image URL here */
    background-size: cover; /* Cover the entire background with the image */
    background-position: center; /* Center the image */
}

    
    nav {
        background-color:   #000000;
        color: #ffffff;
        padding: 1em;
        text-align: center;
    }
    
   
    .nav-container {
        max-width: 1200px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: #ffffff;
    }
    
    .nav-logo {
        font-size: 24px;
        font-weight: bold;
        margin-right: 20px;
    }
    
    nav ul {
        list-style: none;
        margin: 0;
        padding: 0;
        display: flex;
        color: white;
    }
    
    nav li {
        margin-right: 20px;
        color: white;
    }
    
    nav a {
        color: #ffffff;
        text-decoration: none;
    }
    
    nav a:hover {
        color: #ffffff;
    }
    
    #hero {
        background-size: cover;
        background-position: center;
        height: 80vh;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #ffffff;
    }
    
    .hero-container {
        max-width: 800px;
        margin: 0 auto;
        text-align: center;
    }
    
    #hero h1 {
        font-size: 48px;
        font-weight: bold;
        margin-bottom: 20px;
    }
    
    #hero p {
        font-size: 18px;
        margin-bottom: 20px;
    }
    
    #hero button {
        background-color: #000000;
        color: #fff;
        border: none;
        padding: 10px 20px;
        font-size: 18px;
        cursor: pointer;
    }
    
    #hero button:hover {
        background-color: #000000;
    }
    
    #about {
        padding: 2em;
        background-color: #000000; 
        color: white;
    }
    
    .about-container {
        max-width: 800px;
        margin: 0 auto;
        text-align: center;
    
    }
    
    #about h2 {
        font-size: 36px;
        font-weight: bold;
        text-align: center;
        margin-bottom: 20px;
    }
    
    #about p {
        font-size: 18px;
        line-height: 1.6;
        margin-bottom: 20px;
    }

    .features-container {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: 10px;
        padding: 2em;
    }

    .feature {
        background-color: #fff;
        border-radius: 8px;
        padding: 10px;
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
        flex: 1 0 300px;
        text-align: center;
    }

    .feature i {
        font-size: 3em;
        color: #333;
        margin-bottom: 10px;
    }

    .feature h3 {
        font-size: 1.5em;
        font-weight: bold;
        margin-bottom: 10px;
    }

    .feature p {
        font-size: 1em;
        line-height: 1.5;
    }

    .cta {
        background-color:  #2e2d2d;
        padding: 10px;
        text-align: center;
        background-image: url(image.png); 
        background-size: cover;
        background-position: center; 
    }

    .cta h2 {
        font-size: 24px;
        margin-bottom: 8px;
    }

    .cta form {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .cta label {
        margin-bottom: 10px;
    }

    .cta input {
        padding: 10px;
        margin-bottom: 10px;
        border: 1px solid #ffffff;
        border-radius: 5px;
        width: 80%;
        max-width: 200px;
    }

    .cta button[type="submit"] {
        background-color:  #680900;
        color: #ffffff;
        padding: 10px 20px;
        border: none;
        border-radius: 5px;
        cursor: pointer;
    }

    .cta button[type="submit"]:hover {
        background-color: #b90600;
    }
    
    #footer {
        background-color:   #000000;
        color: #ffffff;
        padding: 1em;
        text-align: center;
    }
    
    .footer-container {
        max-width: 500px;
        margin: 0 auto;
    }